<template>
<div>
	<svg id="SVGicon" style="position:absolute;width: 0px;height: 0px;" version="1.1" xmlns="http://www.w3.org/2000/svg">
		<symbol viewBox="0 0 1024 1024" id="icon-publish">
			<title>发布</title>
			<path d="M851.2 166.4c-12.8-6.4-25.6-6.4-32 0l-640 320c-12.8 0-19.2 12.8-19.2 25.6s6.4 25.6 12.8 25.6l153.6 96c12.8 6.4 25.6 6.4 38.4-6.4L704 320l12.8 6.4-307.2 326.4c-6.4 6.4-6.4 12.8-6.4 19.2v140.8c0 12.8 6.4 25.6 19.2 32 12.8 6.4 25.6 0 32-6.4l76.8-76.8L684.8 864c6.4 6.4 12.8 6.4 19.2 6.4h12.8c12.8-6.4 19.2-12.8 19.2-25.6l128-640c0-19.2 0-32-12.8-38.4z"></path>
		</symbol>
	</svg>
	<ul class="chooseTab">
		<router-link to="/Search?will=Rent" tag="li" id="gameChoose">当前选择：{{this.$store.state.curSelect}}</router-link>
		<router-link to="/Publish" class="toPublish" active="false" ><svg class="icon-send"><use xlink:href="#icon-publish"></use></svg>发布账号</router-link>
	</ul>
	<div class="content" ref="content">
	  	<ul class="list" slot="list">
	        <li  v-for="(item,index) of rentList" :key="''+Math.random()" >
	          <router-link tag="a" :to="'/Rent/detail?Rid='+item.id" >
	  	        <div class="imgbox">
	  	        	<img v-lazy="item.imgUrl">
	  	        </div>
	  	        <div class="info">
	  		        <h4 class="desc"><i class="bluef">{{item.lease}}小时</i>起租</h4>
	  		        <p class="title"><span class="bluef">[{{item.gameName}}]</span>{{item.title}}</p>
	  		        <p class="count_data">
	  		        	<em><span class="bluef">￥{{item.price}}小时</span>押金{{item.foreMoney}}</em>
	  		        	<em>累计出租{{item.rentTimes}}次</em>
	  		        </p>
	  	        </div>
	          </router-link>
	        </li>
	  	</ul>
	  	<infinite-loading :identifier="infiniteId" @infinite="infiniteHandler">
			<div class="onloading" slot="spinner"><b></b>正在加载数据...</div>
			<div class="tc" slot="no-more">我是有底线的</div>
			<div class="noDataTips" slot="no-results">暂无数据</div>
	  	</infinite-loading>
	</div>
	<myfooter></myfooter>
</div>
</template>
<script>
	var isFromC;
	import axios from "axios";
	import  "@/assets/css/animate.css";
	import {mapState} from "vuex";
	import myfooter from  "common/footer.vue";


	export default {
			data (){
				return{
					hasRequest:false,
					rentList:[],
					page: 1,
					lastGameId:"",
					allData:{"data":{}},
					infiniteId: +new Date()
				}
			},

			computed:{
				...mapState(['gameId'])
			},
			name:"index",
			methods:{
				infiniteHandler($state) {
					 setTimeout(()=>{
					  axios.get("/api/rent.json", {
					    params: {
					      page: this.page,
					      type:this.gameId
					    }
					  }).then((res) => {
						  res=res.data;
						  if(!this.hasRequest){
						  	this.dataTransform(res.data.all);
						  	this.hasRequest=true;
						  }
						  var reallyres=this.allData.data
						  if(res.flag&&reallyres[this.gameId]&&reallyres[this.gameId][this.page-1]){
						  		const data=reallyres[this.gameId][this.page-1];
						  		this.rentList=this.rentList.concat(data);
						  		this.page += 1;
						  		$state.loaded();
						    } else {
						      $state.complete();
						    }
					  })},200)
				},
				dataTransform (all){
					var newobj={"all":all}
					all.forEach(function(ele) {
						if(!newobj[ele.gameId]){
							newobj[ele.gameId]=[]
						}
						newobj[ele.gameId].push(ele);
					})
					for(var i in newobj){
						var list=newobj[i];
						var newlist=[]
						list.forEach(function(ele,index) {
							var page=Math.floor(index/10);
							if(!newlist[page]){
								newlist[page]=[]
							}
							newlist[page].push(ele)
						})
						newobj[i]=newlist;
					}
					this.allData.data=newobj;
				},
				changeType (){
					this.page=1;
					this.rentList=[];
					this.infiniteId += 1;
				}
	
			},
			watch:{
				gameId: function() {
					this.changeType()
					this.lastGameId=this.gameId;
				}
			},

			activated (){
				if(isFromC){
					this.$refs['content'].scrollTop=sessionStorage.pos;
				}
			},
			components:{
				myfooter
			}
			,
			beforeRouteLeave (to, from, next) {
				if(to.name=="rentDetail"){
					sessionStorage.pos=this.$refs['content'].scrollTop
				}
				next()
			},
			beforeRouteEnter(to, from, next) {
			  if (from.name != 'rentDetail') {
			    isFromC = false;
			  } else {
			    isFromC = true;
			  }

			  next();
			}
	}
</script>
<style scoped>
	@import url("../../assets/css/rent.css");
	svg{
		fill:currentColor;
		width: 0.3rem;
		height: 0.3rem;
	}
	.toPublish{
		color: #fff;
		display: flex;
		position: absolute;
		right: 20px;
		align-items: center;
		animation: bounceInRight 1s;
	}
</style>


